<template>
    <div>
        <img class="index" src="../assets/images/start.png">
    </div>
</template>
<script>
    require('../assets/scss/CV.scss');
    require('../assets/scss/iconfont/iconfont.css');
    require('../assets/scss/github-markdown.css');

    export default {
        mounted() {
            setTimeout(() => {
                this.$router.push({
                    name: 'home'
                });
            }, 3000);
        }
    };
</script>
<style lang="scss" scoped>
    .index{
        width: 100%;
        height: 100%;
        display: block;
        position: absolute;
    }
</style>



